import { useState, useLayoutEffect } from "react";
import Container from "../../../components/Container/Container";
import DropdownIsRight from "../components/DropdownIsRight/DropdownIsRight";
import DropDownThemes from "../components/DropdownThemes/DropDownThemes";
import {
  StyledLi,
  StyledListAnswers,
  StyledUl,
  StyledSpan,
  StyledSpanResult,
  StyledSum,
  StyledSection
} from "./UserAnswers.Styled";

interface IUserAnswer {
  point: number;
  userAnswer: string;
  question: string;
  theme: string;
}

interface IUserAnswers {
  [key: string]: IUserAnswer;
}

interface IAnswer {
  [key: string]: string;
}

interface IAllAnswers {
  [key: string]: {
    [key: string]: IAnswer;
  };
}

interface IQuestion {
  descr: string;
  name: string;
  rightAnswer: string;
  theme: string;
}

interface IAllQuiestions {
  [key: string]: {
    [key: string]: IQuestion;
  };
}

const UserAnswers = () => {
  const [userAnswers, setUserAnswers] = useState<IUserAnswers>({
    answer1: {
      point: 1,
      userAnswer: "E.  ",
      question: "      ?",
      theme: " , ",
    },
    answer2: {
      point: 0,
      userAnswer: "E.  ",
      question:
        "       (static)?",
      theme: " , ",
    },
  });

  const [allQuiestions, setAllQuiestions] = useState<IAllQuiestions>({
    question1: {
      deu: {
        descr: "https://html5book.ru/otzyvchivyj-dizayn-saita/",
        name: "Was ist der Unterschied zwischen adaptiven und responsiven Designs?",
        rightAnswer:
          "A. Responsives Design  ein Satz von Dateien (CSS, HTML, JS) fr alle Gerte mit einem flexiblen Layoutraster, flexiblen Bildern und Medienabfragen, Responsive Design  ein separater Satz von Dateien fr jeden Gertetyp",
        theme: "Web-Theorie",
      },
      en: {
        descr: "https://html5book.ru/otzyvchivyj-dizayn-saita/",
        name: "What is the difference between adaptive and responsive designs?",
        rightAnswer:
          "A. Responsive design - one set of files (css, html, js) for all devices using a flexible layout grid, flexible images and media queries, responsive design - a separate set of files for each type of device",
        theme: "Web Theory",
      },
      ru: {
        descr: "https://html5book.ru/otzyvchivyj-dizayn-saita/",
        name: "     (adaptive)   (responsive) ?",
        rightAnswer:
          "A.       (css, html, js)        ,    ,          ",
        theme: " ",
      },
    },
    question2: {
      deu: {
        descr: "https://html5book.ru/otzyvchivyj-dizayn-saita/",
        name: "Was ist der Unterschied zwischen adaptiven und responsiven Designs?",
        rightAnswer:
          "A. Responsives Design  ein Satz von Dateien (CSS, HTML, JS) fr alle Gerte mit einem flexiblen Layoutraster, flexiblen Bildern und Medienabfragen, Responsive Design  ein separater Satz von Dateien fr jeden Gertetyp",
        theme: "Web-Theorie",
      },
      en: {
        descr: "https://html5book.ru/otzyvchivyj-dizayn-saita/",
        name: "What is the difference between adaptive and responsive designs?",
        rightAnswer:
          "A. Responsive design - one set of files (css, html, js) for all devices using a flexible layout grid, flexible images and media queries, responsive design - a separate set of files for each type of device",
        theme: "Web Theory",
      },
      ru: {
        descr: "https://html5book.ru/otzyvchivyj-dizayn-saita/",
        name: "     (adaptive)   (responsive) ?",
        rightAnswer:
          "A.       (css, html, js)        ,    ,          ",
        theme: " ",
      },
    },
  });

  const [allAnswers, setAllAnswers] = useState<IAllAnswers>({
    answers1: {
      deu: {
        A: "fr alle Gerte",
        B: "fr alle Gerte",
        C: "fr alle Gerte",
        D: "fr alle Gerte",
        E: "fr alle Gerte",
      },
      en: {
        A: "all devices",
        B: "all devices",
        C: "all devices",
        D: "all devices",
        E: "all devices",
      },
      ru: {
        A: " ",
        B: " ",
        C: " ",
        D: " ",
        E: " ",
      },
    },
    answers2: {
      deu: {
        A: "jeden Gertetyp",
        B: "jeden Gertetyp",
        C: "jeden Gertetyp",
        D: "jeden Gertetyp",
        E: "jeden Gertetyp",
      },
      en: {
        A: "one layout",
        B: "one layout",
        C: "one layout",
        D: "one layout",
        E: "one layout",
      },
      ru: {
        A: "",
        B: "",
        C: "",
        D: "",
        E: "",
      },
    },
  });

  return (
    <Container>
      <details open>
        <StyledSum></StyledSum>
        <StyledSection>
          <DropDownThemes />
          <DropdownIsRight />
        </StyledSection>

        <StyledUl>
          {Object.entries(userAnswers).map((userAnswerArr, index) => {
            const numbQuiestion = userAnswerArr[0].match(/\d+/);
            const { point, userAnswer, question, theme } = userAnswerArr[1];
            const isRight = point ? true : false;
            const color = isRight ? "green" : "red";
            const className = {
              borderRadius: 10,
              border: `1px solid ${color}`,
            };
            const lang = localStorage.getItem("i18nextLng");

            return (
              <StyledListAnswers style={className} key={index + 1}>
                <StyledUl>
                  <StyledSpanResult isRight={isRight}>
                    {isRight ? "\u2714" : "\u2718"}
                  </StyledSpanResult>
                  <StyledLi>
                    <StyledSpan>{`  ${numbQuiestion}: `}</StyledSpan>
                    {question}
                  </StyledLi>
                  <StyledLi>
                    <StyledSpan> : </StyledSpan>
                    {Object.entries(
                      allAnswers[`answers${numbQuiestion}`][lang || "ru"]
                    )
                      .map((answer) => answer.join(", "))
                      .join(", ")}
                  </StyledLi>
                  <StyledLi>
                    <StyledSpan> :</StyledSpan> {userAnswer}
                  </StyledLi>
                  {["rightAnswer", "descr"].map((property, index) => {
                    return (
                      <StyledLi key={index + 1}>
                        <StyledSpan> : </StyledSpan>
                        {
                          allQuiestions[`question${numbQuiestion}`][
                            lang || "ru"
                          ][property]
                        }
                      </StyledLi>
                    );
                  })}
                </StyledUl>
              </StyledListAnswers>
            );
          })}
        </StyledUl>
      </details>
    </Container>
  );
};

export default UserAnswers;
